.map {
  background-color: #777;
}

.mapplic-sidebar {
  top: 80px;
  left: 64px;
  z-index: 50;
  width: 414px;
  padding: 0;
  pointer-events: none;
}

@media (max-width: 1299px) {
  .mapplic-sidebar {
    left: 72px;
  }
}

@media (max-width: 1199px) {
  .mapplic-sidebar {
    left: 64px;
  }
}

@media (max-width: 991px) {
  .mapplic-sidebar {
    top: 24px;
    left: 48px;
    width: 320px;
  }
}

@media (max-width: 759px) {
  .mapplic-sidebar {
    left: 50%;
    width: 272px;
    transform: translateX(-50%);
  }
}

.mapplic-container {
  width: 100%;
}

.mapplic-sidebar-header {
  right: 0;
  left: 0;
  padding: 0;
  background: transparent;
  box-shadow: none;
}

.mapplic-sidebar-tagsrow .mapplic-list-container {
  visibility: visible;
  opacity: 1;
}

.mapplic-sidebar-header-opened .mapplic-list-container {
  visibility: hidden;
  opacity: 0;
}

.mapplic-list-container-outer {
  position: absolute;
  right: 0;
  left: 0;
  top: 122px;
  border-radius: 15px;
  overflow: hidden;
  pointer-events: none;
}

.mapplic-list-container {
  pointer-events: auto;
  visibility: hidden;
  opacity: 0;
  position: static;
  border-radius: 15px;
  bottom: unset;
  overscroll-behavior: contain;
  max-height: 470px;
}

@media (max-width: 1199px) {
  .mapplic-list-container {
    max-height: 350px;
  }
}

.mapplic-list-container.-hide {
  visibility: hidden;
  opacity: 0;
}

.mapplic-list-container.-show {
  visibility: visible;
  opacity: 1;
}

.mapplic-list-container li {
  padding: 15px;
}

.mapplic-list-container li + li {
  margin-top: -20px !important;
}

.mapplic-list-container::-webkit-scrollbar-track {
  margin: 5px 0;
}

.mapplic-search-input {
  height: 72px !important;
  padding-left: 48px !important;
  font-size: 18px !important;
  line-height: 22px !important;
  color: #000;
  background: #fff !important;
  border-radius: 15px;
}

.mapplic-search-input::placeholder {
  color: #c4c4c4;
}

.mapplic-filter {
  margin: 0;
  padding: 15px 0;
  margin-top: 24px;
  background: #fff !important;
  border-radius: 15px;
}

.mapplic-list-category + .mapplic-list-category {
  margin-top: 15px;
}

.mapplic-list-category > a {
  margin: 0;
  padding: 5px 20px;
}

.mapplic-list-category > a .mapplic-thumbnail-placeholder {
  margin-right: 8px;
}

.mapplic-dir-item > a {
  display: flex;
  align-items: center;
  min-height: 80px;
  padding: 0 28px 0 0 !important;
  background: transparent;
  border: none;
  border: 1px solid #c4c4c4;
  border-radius: 15px;
}

@media (max-width: 991px) {
  .mapplic-dir-item > a {
    padding: 0 16px 0 0 !important;
  }
}

@media (max-width: 759px) {
  .mapplic-dir-item > a {
    min-height: 58px;
  }
}

.mapplic-dir-item > a img {
  width: 78px;
  height: 78px;
  margin-right: 30px;
  border-radius: 15px;
}

@media (max-width: 759px) {
  .mapplic-dir-item > a img {
    width: 56px;
    height: 56px;
    margin-right: 24px;
  }
}

.mapplic-dir-item > a h4 {
  font-size: 18px !important;
  line-height: 22px !important;
  color: #000;
}

@media (max-width: 759px) {
  .mapplic-dir-item > a h4 {
    font-size: 16px !important;
    line-height: 19px !important;
  }
}

.mapplic-dir-item > a .mapplic-about {
  margin-left: auto;
  font-size: 14px !important;
  line-height: 17px !important;
  text-transform: uppercase;
}

.mapplic-map-image .mapplic-pin {
  display: none;
}

.mapplic-level-switcher {
  margin: 80px 64px;
}

@media (max-width: 1299px) {
  .mapplic-level-switcher {
    margin: 80px 72px;
  }
}

@media (max-width: 1199px) {
  .mapplic-level-switcher {
    margin: 24px 64px;
  }
}

@media (max-width: 759px) {
  .mapplic-level-switcher {
    margin: 120px 24px;
  }
}

.mapplic-level-switcher button {
  width: 72px;
  height: 72px;
  font-size: 18px;
  font-weight: bold;
  line-height: 22px;
  border-radius: 15px;
  transition: 0.1s;
}

@media (max-width: 759px) {
  .mapplic-level-switcher button {
    width: 52px;
    height: 52px;
  }
}

.mapplic-level-switcher button:hover {
  color: #fff;
  background: #000;
}

.mapplic-level-switcher button.mapplic-selected {
  color: #fff;
  background: #000;
  transform: none;
}

.mapplic-level-switcher button + button {
  margin-top: 15px;
}

g.dark.mapplic-clickable:hover > * {
  fill: #fff;
}

.mapplic-sidebar-header .mapplic-icon-magnifier {
  display: none;
}

.mapplic-sidebar-filterable .mapplic-search-toggle {
  display: none;
}

.mapplic-zoom-buttons {
  top: 585px;
  right: 64px;
  margin: 0;
}

@media (max-width: 1299px) {
  .mapplic-zoom-buttons {
    top: 494px;
  }
}

@media (max-width: 1199px) {
  .mapplic-zoom-buttons {
    top: 378px;
  }
}

@media (max-width: 759px) {
  .mapplic-zoom-buttons {
    top: 400px;
    right: 24px;
  }
}

.mapplic-zoom-buttons button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 72px;
  height: 72px;
  border-radius: 15px;
  transition: 0.1s;
}

@media (max-width: 759px) {
  .mapplic-zoom-buttons button {
    width: 52px;
    height: 52px;
  }
}

.mapplic-zoom-buttons button:hover {
  color: #fff;
  background: #000;
}

.mapplic-zoom-buttons button:hover svg {
  fill: #fff;
}

.mapplic-zoom-buttons button + button {
  margin-top: 15px;
}

.mapplic-zoom-buttons button svg {
  position: static;
  width: 20px;
  height: 20px;
  transition: 0.1s;
}

.mapplic-external {
  position: absolute;
  top: 176px;
  left: 64px;
  z-index: 50;
}

@media (max-width: 1299px) {
  .mapplic-external {
    top: 176px;
    left: 76px;
  }
}

@media (max-width: 1199px) {
  .mapplic-external {
    top: 176px;
    left: 64px;
  }
}

@media (max-width: 991px) {
  .mapplic-external {
    top: 120px;
    left: 50px;
  }
}

@media (max-width: 759px) {
  .mapplic-external {
    left: 50%;
    transform: translateX(-50%);
  }
}

.mapplic-external .tenant-item {
  width: 305px;
  height: 305px;
}

@media (max-width: 1199px) {
  .mapplic-external .tenant-item {
    width: 318px;
    height: 318px;
  }
}

@media (max-width: 759px) {
  .mapplic-external .tenant-item {
    width: 300px;
    height: 300px;
  }
}

.dark.mapplic-clickable {
  transition: 0.1s;
}

.dark.mapplic-clickable:not(g):not(.map-additional-icons):hover path:nth-child(1),
g.dark.mapplic-clickable:not(.map-additional-icons):hover > * path:nth-child(1) {
  fill: #1977f3 !important;
  stroke: #1977f3 !important;
}

.dark.mapplic-clickable:not(g):not(.map-additional-icons):hover path:nth-child(2),
g.dark.mapplic-clickable:not(.map-additional-icons):hover > * path:nth-child(2) {
  fill: #fff !important;
}

.dark.mapplic-clickable#accessibility:not(g):not(.map-additional-icons):hover path:nth-child(1),
g.dark.mapplic-clickable:not(.map-additional-icons):hover > * path:nth-child(1) {
  fill: #1977f3 !important;
  stroke: #1977f3 !important;
}

.dark.mapplic-clickable#accessibility:not(g):not(.map-additional-icons):hover path:nth-child(2),
g.dark.mapplic-clickable:not(.map-additional-icons):hover > * path:nth-child(2) {
  fill: #fff !important;
}

g.dark.mapplic-clickable:not(.map-additional-icons):hover path:nth-child(1) {
  fill: #1977f3 !important;
  stroke: #1977f3 !important;
}

g.dark.mapplic-clickable:not(.map-additional-icons):hover path:nth-child(2) {
  fill: #fff !important;
}

.mapplic-pin.mapplic-active,
.mapplic-pin.mapplic-highlight {
  display: none !important;
  stroke: #000;
}

.mapplic-clickable.map-additional-icons:hover > rect {
  fill: #f3c319 !important;
}

.mapplic-clickable.-focused path:nth-child(1) {
  fill: #1977f3;
  stroke: #1977f3;
}

.mapplic-clickable.-focused path:nth-child(2) {
  fill: #fff;
}

.mapplic-tooltip {
  transition: none !important;
}

.mapplic-tooltip::after {
  content: none !important;
}

.mapplic-tooltip-wrap {
  border-radius: 30px;
  padding: 5px;
  background: #1977f3;
  position: relative;
  bottom: 15px;
}

.mapplic-tooltip-wrap.-yellow {
  background: #f3c319;
}

.mapplic-tooltip-wrap::after {
  content: none !important;
}

.mapplic-hovertip .mapplic-tooltip-title {
  font-size: 8px;
  line-height: 10px;
  letter-spacing: 0.04em;
  color: #fff;
  text-transform: uppercase;
}

.mapplic-tooltip-triangle {
  display: none;
}

.mapplic-dir-item.-hide-forever {
  display: none !important;
}

.mapplic-dir-item.-no-margin-top {
  margin-top: 0 !important;
}

.dark.mapplic-pin.mapplic-highlight {
  background-color: #000 !important;
}

.mapplic-level-switcher button.mapplic-selected {
  z-index: 40;
}

.mapplic-sidebar-dim {
  visibility: hidden;
}

.mapplic-external__close {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 1;
  display: flex !important;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #000;
  border: none;
  border-radius: 50%;
}

.mapplic-external__close:hover {
  background: #fff;
}

.mapplic-external__close:hover svg {
  fill: #000;
}

.mapplic-external__close.-hidden {
  display: none !important;
}

.mapplic-external__close svg {
  transition: 0.1s;
}

.mapplic-list-category .mapplic-thumbnail {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
}

.mapplic-tooltip-content {
  display: none;
}

.mapplic-thumbnail-placeholder {
  width: 78px;
  height: 78px;
  margin-right: 30px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}
